/* VARIABLES */
#root {
  --color-red: #EE0909;
  --color-white: #ffffff;
  --color-yellow: #E87E00;
  --color-bright-yellow: #d4a821;
  --color-brik-red: #D83F31;
  --color-dark-purple: #28181B;
  --color-light-purple: #BA9A93;
  --color-green: #228b6e;
  --color-purple: #a52a96;
  --bgcolor: var(--color-green);
  --color-light-yellow: #D19E60;
  --color-black: #111211;
  --color-shadow: rgba(0, 255, 157, 0.7);
  --color-gray: #D9D9D9;
  --nav-font: 'Alegreya';
  --nav-button: 'Galindo';
  --title-font: 'Kanit';
  --button-font: 'Roboto Condensed';
  --name-font: 'Happy Monkey';
  --normal-text-font: 'Cabin';


  background-color: var(--color-dark-purple);
  color: black;
}

/* ----------------------------------------------------------------------------------------------------- */

/* APP */

.App{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
}

#page-body{
  border-radius: 0 0 2rem 2rem;
  background-color: var(--bgcolor);
  margin-top: auto;
}
/* ----------------------------------------------------------------------------------------------------- */

/* NAVIGATION BAR */

.nav_bar {
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  gap: .5rem;
  background-color: var(--color-dark-purple);
  border-radius: 0 0 2rem 2rem;
}

.nav_link {
  color: white;
  font-family: var(--nav-font);
  font-size: 1.2rem;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  align-self:center;
  text-decoration: none;
  border: .1rem solid white;
  background-color: var(--color-brik-red);
  width: 18%;
  padding:.5rem .5rem;
  transition:all .5s ease;
  letter-spacing:1px;
  outline:none;

}

#left_outer_link{
  border-radius: .5rem .5rem .5rem 5rem;
}

#right_outer_link{
  border-radius: .5rem .5rem 2rem .5rem;
}

.inner_nav{
  border-radius: .5rem;
}

.nav_link:hover {
  background-color: var(--color-yellow);
  color: black;
  font-size: 1.5rem;
}

.nav_link:active{
  background-color: var(--color-yellow);
  color: black;
}

@media(min-width: 800px){
  .nav_link{
    width: 23%;
  }
}
/* ----------------------------------------------------------------------------------------------------- */
/* ALL PAGE */

.margined-page{
  margin: 2rem 2rem;
  height: 100%;
}

.wrapped-page{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
} 


.left-border-decor{
  background-color: var(--color-dark-purple);
  margin-right: 2rem;
}

.submit-button{
  font-family: var(--button-font);
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--color-yellow);
  letter-spacing: .02rem;
  align-self: center; 
  background-color: var(--color-green);
  cursor: pointer;
  border: .2rem solid currentColor;
  border-radius: .8rem;
  padding: .4rem 1rem;
  position: relative;
  transition: color .3s;
  z-index: 1;

}

.submit-button::before, .submit-button::after {
    content: '';
    position: absolute;
    background-color: var(--color-green);
    z-index: -1;
    transition: all .3s;

  }
.submit-button::before {
  width: calc(100% - 3rem);
  height: calc(101% + 1rem);
  top: -.5rem;
  left: 50%;
  transform: translateX(-50%);
}
.submit-button::after {
  height: calc(100% - 2rem);
  width: calc(101% + 1rem);
  left: -.5rem;
  top: 50%;
  transform: translateY(-50%);
}
.submit-button:hover {
  color: white;
}
.submit-button:hover::before { width: 0; }
.submit-button:hover::after { height: 0; }
.submit-button:active { transform: translateY(.2rem); }

.normal_text{
  font-size: 18px;
  text-align: justify;
  letter-spacing: 0.7px;
}

/* ----------------------------------------------------------------------------------------------------- */

/* HOME PAGE */

/* greetings */
.aboutMe-greeting{
  flex: 40%;
  display: flex;
  flex-direction: column;
  font-family: var(--button-font);
  font-weight: 400;
  font-size: 1.5rem;
}

.aboutMe-greeting-container{
  grid-area: greeting;
  display: flex;
  align-items: center;
  width: 100%;
}

/* image */
.home-photo-container{
  grid-area: image;
  height: 15rem;
  justify-self: center;
  padding: 2rem 0;
}


/* description */

.aboutMe-author{
  font-family: var(--nav-font);
  font-weight: 400;
  font-size: 2.5rem;
  letter-spacing: 6px;
  text-align: center;
}

#aboutMe-desc{
  text-align: center;
}

.aboutMe-contact-container{
  padding-top: 2rem;
  display: flex;
  width: 100%;
  gap: 6rem;
  justify-content: center;
}
.aboutMe-tag-container{
  display: flex;
  gap: 2rem;
}

#aboutMe-resume{
  font-size: 1.2rem !important;
}

.aboutMe-contact-tags{
  font-size: .5rem !important;
  color: var(--color-yellow);
}

.aboutMe-profile_desc{
  grid-area: desc;
  display: flex;
  flex-direction: column;
  font-family: var(--normal-text-font);
  gap: 1.2rem;
}

/* closing */
#horizontal-line-home{
  width: 70%;
}

.title-tag{
  display: flex;
  flex-direction: column;
  font-family: var(--button-font);
  text-align: right;
}
#aboutMe-greeting-closing{
  font-size: 1.3rem; 
}
.career-path{
  font-size: 2rem;
  /* width: 80%; */
  font-weight: 200;
  color: white;

}

.aboutMe-document-closing{
  grid-area: closing;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 8%;
  padding-top: 2rem;
}


/* home page display */
.home-page{
  display: grid;
  grid-template-areas: 
  "greeting" 
  "image"
  "desc"
  "closing"
  ;
}

@media(min-width: 800px){
  .aboutMe-author{
    font-weight: 600;
    font-size: 3.5rem;
    text-align: left;
    padding: 2rem 0;
  }
  #aboutMe-desc{
    text-align: left;
  }
  .aboutMe-contact-tags{
    font-size: 1rem !important;
  }
  #aboutMe-resume{
    font-size: 2rem !important;
  }
  .home-photo-container{
    height: 25rem;
  }
  .aboutMe-greeting{
    font-size: 2rem!important;
  }
  .home-page{
    grid-template-areas: 
    "greeting greeting" 
    "desc image"
    "closing closing"
    ;
  }

}
/* ----------------------------------------------------------------------------------------------------- */

/* SKILLS PAGE */
.work-left-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.work-photo-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.work-photo{
  max-height: 40%;
}

.work-citation{
  flex:0.7;
  display: flex;
  flex-direction: column;
  font-family: var(--normal-text-font);
  font-size: 1.2rem;
  gap: 1.5rem;
}
.work-testimony-message{
  text-align: center;
}
.work-quote-decor{
  display: flex;
  gap: 0.8rem;

}
.work-testimony, .work-testimony-author{
  width: 90%;
  padding: 1rem;
}
.quote-icons{
  font-size: 1.5rem;
}
#quote-left{
  padding-right: 1rem;
}
#quote-right{
  padding-left: 1rem;
}


.work-testimony-author{
  text-align: right;
}
.work-right-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  gap: 2rem;
  --barItem-font-size: .6rem;
}

.work-skills_icons{
  width: 80%;
  display: flex;
  justify-content: space-between;
}

.work-button{
  border: 10px dashed var(--color-yellow);
  border-radius: 10rem;
  padding: 4px 8px;
  /* margin: 10px 2px; */
  width: 6rem;
  aspect-ratio: 1/1;
  font-family: var(--button-font);
  color: white;
  font-size: .8rem;
  text-align: center !important;
  background-color: var(--color-dark-purple);
}

.work-button:hover {
  border: 10px solid var(--color-yellow);
}
.work-button:active { 
  transform: translateY(.2rem);
 }

 @media(min-width: 800px){
  .work-right-container{
    --barItem-font-size: 1.1rem;
  }
  .work-skills_icons{
    justify-content: center;
    gap: 3rem;
  }
  .work-button{
    width: 9rem;
    font-size: 1.1rem;
  }
 }
/* ----------------------------------------------------------------------------------------------------- */

/* EXPERIENCE PAGE */


.experienc-main-container{
  --exp-card-color : #C4B6A5;
  gap: .8rem;
  height: 100%;
}

.experience-column{
  --card-height: 350px;
  --card-width: 515px;
  width: var(--card-width);
  height: var(--card-height);
  background-color: rgba(5, 48, 28, 0.5)  ;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 0;
  display: flex;
  transition: all .3s;
  box-shadow: 0px 1px 2px 0px rgba(0,255,255,0.7),
              1px 2px 4px 0px rgba(0,255,255,0.7),
              2px 4px 8px 0px rgba(0,255,255,0.7),
              2px 4px 16px 0px rgba(0,255,255,0.7);
            }

.experience-image{
  /* margin: 1rem; */
  height: calc(var(--card-height) - 20px);
  width: calc(var(--card-width) - 20px);
  opacity: 0.9;
}

.experience-image-text{
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  position: absolute;
  align-self: center;
  z-index:1;
  width: 90%;
  height: 100%;
  display: flex;
  gap: .2rem;
  justify-content: center;
  align-items: center;
  font-family: var(--button-font);
  font-size: 1.2rem;
  opacity: 0;
  color: white;
}

.experience-skill-container{
  padding-top: .8rem;
  padding-bottom: .8rem;
  display: flex;
  gap: .7rem;
}

.experience-position{
  padding-bottom: .8rem;
}
#experience-horLine{
  background-color: white;
}
.experience-company, .experience-position{
  font-size: 1.3rem;
  font-weight: 700;
}
.experience-date{
  font-size: 1.3rem;
  padding-bottom: .5rem;
}
.experience-desc{
  padding-bottom: .9rem;
  text-align: center;
}
.experience-button{
  color: white;
  font-family: var(--nav-button);
  font-weight: 400;
  text-decoration: none;
  border: .2rem solid white;
  border-radius: .8rem;
  padding: .5rem 4rem;
}
.experience-button:hover{
  background-color: var(--color-yellow);
  color: white;
}
.experience-button:active{
  translate: 0 .5rem;
}

.experience-code-tag{
  color: white;
  font-family: var('--font-button');
  font-size: 1.2rem;
  font-weight: bold;
  background-color: var(--color-brik-red);
  padding: .5rem .8rem;
  border-radius: .5rem;
}
.experience-app-type{
  color: var(--color-red)
}

.experience-column:hover{
  background-color: var(--bgcolor);
  border-color: var(--color-yellow);
  border-radius: 1rem;
}


.experience-column:hover .experience-image{
  transition: all .3s;
  opacity: .1;
}
.experience-column:hover .experience-image-text{
  transition: all .3s;
  opacity: 1;
}


/* ----------------------------------------------------------------------------------------------------- */

/* GET IN TOUCH PAGE */

.getInTouch-center-container{
  gap: 2.5rem;
}
.getInTouch-column{

  display: flex;
  flex-direction: column;
  font-family: var(--normal-text-font);
  padding-bottom: 2rem;
}

.getInTouch-photo-container{
  width: 100%;
  height: 50%;
  padding-bottom: 3rem;
}

.getInTouch-contact-info{
  font-family: var(--normal-text-font);
  padding-bottom: 1.5rem;
}
.getInTouch-tag{
  font-size: 1.2rem;
  padding-bottom: 1.5rem;
}
.getIntouch-tag-type{
  font-family: var(--button-font);
  font-weight: bold;
  padding-left: .5rem;
  font-size: 1.5rem;
}

.getInTouch-call-icons{
 display: flex;
 gap: .6rem;
 align-items: center;
}
.decor-icons{
  color: var(--color-dark-purple);
}

.getInTouch-info{
  font-size: 1.2rem;
}

#column-right{
  /* github pages : can't host full stack, so removed contact form*/
  /* justify-content: flex-end;
  width: 40rem; */
  display: none;
}

.getInTouch-title-container{
  display: flex;
  align-items: center;
  padding-bottom: 2rem;
}

.getInTouch-title{
  flex:50%;
  font-weight: 700;
  font-size: 2.6rem;
  font-family: var(--button-font);
}

.getInTouch-form{
  display: flex;
  flex-direction: column;
  gap: .6rem;
}

.getInTouch-error{
  color: var(--color-red);
  font-size: 1.2rem;
}

.getInTouch-input-field, .getInTouch-message-field{
  font-size: 1.1rem;
  border-radius: .5rem;
  background-color: var(--color-light-yellow);
  border: 2px solid black;

}
.getInTouch-input-field{
  height: 2rem;
}

.getInTouch-message-field{
  width: 20rem;
  padding: .2rem .5rem;
  border: 2px solid black;
}

.getInTouch-input-field:focus, .getInTouch-message-field:focus{
  outline: none;
  border: .2rem solid var(--color-red);
}

.getInTouch-message-label{
  font-family: var(--button-font);
  font-size: 1.5rem;
  padding-bottom: .8rem;
}
.getInTouch-message-field{
  font-size: 1rem;
  width: 100%;
  height: 18rem;
  padding: .5rem;
}
.getInTouch-message-container{
  padding-bottom: 1rem;
}


/* ----------------------------------------------------------------------------------------------------- */

/* FOOTER */
footer{
  font-size: .5rem;
  font-family: var(--normal-text-font);
  font-weight: bold;
  text-align: center;
  color: var(--color-light-yellow);
  padding: .8rem 0;
  margin-top: auto;
  
}